import React from "react";
import {
  HomeSection,
  SkillsSection,
  PorfolioSection,
  CareerSection,
} from "../../components";
import FooterSection from "../../components/FooterSection/FooterSection";
import { homeObject } from "./Data";

const Home = () => {
  return (
    <>
      <HomeSection {...homeObject} />
      <PorfolioSection {...homeObject} />
      <SkillsSection {...homeObject} />
      <CareerSection />
      <FooterSection />
    </>
  );
};

export default Home;
